import React, { useState, useEffect } from "react"
import styled from "styled-components";
import Logo from "../img/Logo.svg";
import instagram from "../img/instagram_outlined.svg"
import facebook from "../img/facebook_outlined.svg"
import telegram from "../img/telegram_outlined.svg"
import { Link } from "react-router-dom";

import { useLocation } from "react-router-dom";

const Nav = () => {
  const [scroll, setScroll] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", () => {
      setScroll(window.scrollY > 10)
    })
  }, [])
  return (
    <StyledNav className={scroll ? "main-header scrolled" : "main-header"}>
      <Link id="logo" to="/">
        <img alt="icon" src={Logo} />
      </Link>
      <div className="social">
        <a href="https://t.me/ivan_kozlov_ik" className="social_item">
          <img src={telegram} alt="" className="social_img"/>
        </a>
        <a href="https://www.instagram.com/ivan_kozlov.ik/" className="social_item">
          <img src={instagram} alt="" className="social_img"/>
        </a>
        <a href="https://www.facebook.com/ivanator777" className="social_item">
          <img src={facebook} alt="" className="social_img"/>
        </a>
      </div>
    </StyledNav>
  );
};

const StyledNav = styled.nav`
  height: 10vh;
  display: flex;
  margin: auto;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 10rem;
  background: #1B1B1B;
  position: sticky;
  top: 0;
  z-index: 10;
  transition: all .4s ease-in-out;
  a {
    color: white;
    text-decoration: none;
  }
  #logo{
    height: 9.5vh;
  }
  #logo img{
    width: 9.5vh;
    height: 9.5vh;
    margin: auto 0;
  }
  .social{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 15rem;
    height: 8rem;
  }
  .social_item {
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .3s ease-in-out; 
    margin: 1rem;
    border-radius: 5px;

    &:hover{
      background-color: #262626;
    }
  }
  .social_img{
    width: 2.5rem;
    height: 2.5rem;
  }
}
`;

export default Nav;
